import styled from 'styled-components';

export const SpaceBetweenMenu= styled.div`
display: flex;
align-items: flex-end;
justify-content: space-around;

@media screen and (max-width: 1024px) {
    flex-direction: column;
    align-items: center;
  }
`;

export const CentralMenu = styled.div`
  width: 70vw;
  white-space: nowrap;
  background:  #FFFFFF;

  @media screen and (max-width: 1024px) {
    width:100%;
    order: -9999;
  }
`;

export const LateralMenu = styled.div`
  display: flex;
  flex-direction: column; 
  width: 15vw;
  margin: 0 1vw;
  white-space: nowrap;
  align-items: strech;
  justify-content: stretch; 

  @media screen and (max-width: 1024px) {
    width:100%;
    height:100%;
    margin: 0 1%;
  }
`;

export const MenuHeader = styled.div`
  display: flex;
  align-items: center;
  white-space: nowrap;
  background: #F5AB00;
  color: #fff;

  margin: 0.5vh 0;
  border-radius: 10px 10px 0 0;
  justify-content: center;
  padding: 5px 11px;
`;

export const MenuBody = styled.div`
  display: flex;
  flex-direction: column; 
  align-items: center;
  white-space: nowrap;
  padding: 5px 10px;
  height:70vh;
  justify-content: flex-start;
  background: #FFFFFF;
  border-radius: 0 0 10px 10px;
  overflow-y: scroll;
  overflow-x: hidden;
  width:100%;
  @media screen and (max-width: 1024px) {
    height:100%;
  }
`;

export const FullWidthMenu = styled.div`
  display: flex;
  align-items: center;
  white-space: nowrap;
  padding: 10px 22px;
  width: 100vw;
  justify-content: center;
  background: #EFEFEF;
  margin: 2px;
  @media screen and (max-width: 1024px) {
    background: #FEFEFE;
  }
`;

export const Button = styled.button`
  font-size: 1em;
  margin: 1em;
  padding: 0.25em 1em;
  border-radius: 3px;
  outline: none;
  border: none;
  background: #2DA283;
  &:hover {
    transition: all 0.2s ease-in-out;
    background: #6cd1b6;
    color: #010606;
  }
`;